import React from "react";
import ReactDOM from 'react-dom/client';
import { useAuth0 } from "@auth0/auth0-react";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import sideVideo from '../assets/MATLALA .mp4';
import "../App.css";


const Content = () => {
  const { isAuthenticated } = useAuth0();

  return (
    isAuthenticated && (
          <div className="main_banner">

                    <div className="container">
                      <div className="h1_text col-md-6">
                              <h1>For Live Tv, Series and Podcast, Welcome to MGTV</h1>
                              <h6>Matlala Group Axiz and Cisco discussion</h6>
                              <button className="btn btn-lg btn-primary watch_episode"> Watch Episode </button>
                          </div>
                      </div>

        </div>
    )
  );
};

export default Content;
